.instagram-item {
    background-image: linear-gradient(to left, #fee254e4, #ff9940c1, #ff4069be, #e300a3c4, #522ed57e), url(../../../../public/images/general/section-pattern.png);
    padding: 1.5rem 2rem;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}

.instagram-item button {
    background-color: #fff;
    padding: 0.9rem 0.8rem;
    border-radius: 10px;
    transition: 0.5s;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    color: #2400a6;
    -o-border-radius: 10px;
    -webkit-transition: 0.5s;
    -moz-transition: 0.5s;
    -ms-transition: 0.5s;
    border: 1px solid #fff;
    -o-transition: 0.5s;
}

.instagram-item button:hover {
    background: transparent;
    color: #fff;
}

.instagram-item h2 {
    font-size: 2rem;
    padding-bottom: 0.9rem;
    color: #fff;
    font-weight: bold;
}

.instagram-item p {
    font-size: 1.2rem;
    padding-bottom: 0.9rem;
    color: #fff;
    font-weight: bold;
}